import React from 'react';

function PrivacyPolicy () {
    return (
        <div style={{margin: '30px'}}>
            <p>
                Effective date: December 12, 2018
                <br/>
                Who's your Pupper? ("us", "we", or "our") operates the Who's your Pupper? mobile
                application (hereinafter referred to as the "Service").
                This page informs you of our policies regarding the collection, use, and disclosure of
                personal data when you use our Service and the choices you have associated with that
                data. Our Privacy Policy for Who's your Pupper? is based on the Privacy Policy
                Template from Privacy Policies.
                <br/>
                We use your data to provide and improve the Service. By using the Service, you agree
                to the collection and use of information in accordance with this policy.
                <br/>
                <br/>                
                Information Collection And Use
                <br/>                
                We collect several different types of information for various purposes to provide and
                improve our Service to you.
                <br/>
                <br/>
                Types of Data Collected
                <br/>
                <br/>
                Personal Data
                <br/>
                While using our Service, we may ask you to provide us with certain personally
                identifiable information that can be used to contact or identify you ("Personal Data").
                Personally identifiable information may include, but is not limited to:
                <br/>
                ● Cookies and Usage Data
                <br/>
                <br/>
                Usage Data
                <br/>
                When you access the Service with a mobile device, we may collect certain information
                automatically, including, but not limited to, the type of mobile device you use, your
                mobile device unique ID, the IP address of your mobile device, your mobile operating
                system, the type of mobile Internet browser you use, unique device identifiers and other
                diagnostic data ("Usage Data").
                <br/>
                <br/>
                Tracking &amp; Cookies Data
                <br/>
                We use cookies and similar tracking technologies to track the activity on our Service
                and hold certain information.
                <br/>
                <br/>
                Cookies are files with small amount of data which may include an anonymous unique
                identifier. Cookies are sent to your browser from a website and stored on your device.
                Tracking technologies also used are beacons, tags, and scripts to collect and track
                information and to improve and analyze our Service.
                You can instruct your browser to refuse all cookies or to indicate when a cookie is being
                sent. However, if you do not accept cookies, you may not be able to use some portions
                of our Service. You can learn more how to manage cookies in the Browser Cookies
                Guide.
                <br/>
                Examples of Cookies we use:
                <br/>
                ● Session Cookies. We use Session Cookies to operate our Service.
                <br/>
                ● Preference Cookies. We use Preference Cookies to remember your
                preferences and various settings.
                <br/>
                ● Security Cookies. We use Security Cookies for security purposes.
                <br/>
                <br/>
                Use of Data
                <br/>
                Who's your Pupper? uses the collected data for various purposes:
                ● To provide and maintain the Service<br/>
                ● To notify you about changes to our Service<br/>
                ● To allow you to participate in interactive features of our Service when you choose
                to do so<br/>
                ● To provide customer care and support<br/>
                ● To provide analysis or valuable information so that we can improve the Service<br/>
                ● To monitor the usage of the Service<br/>
                ● To detect, prevent and address technical issues<br/>
                <br/>
                <br/>
                Transfer Of Data
                <br/>
                Your information, including Personal Data, may be transferred to — and maintained on
                — computers located outside of your state, province, country or other governmental
                jurisdiction where the data protection laws may differ than those from your jurisdiction.
                If you are located outside Philippines and choose to provide information to us, please
                note that we transfer the data, including Personal Data, to Philippines and process it
                there.
                <br/>
                Your consent to this Privacy Policy followed by your submission of such information
                represents your agreement to that transfer.
                <br/>
                Who's your Pupper? will take all steps reasonably necessary to ensure that your data is
                treated securely and in accordance with this Privacy Policy and no transfer of your
                Personal Data will take place to an organization or a country unless there are adequate
                controls in place including the security of your data and other personal information.
                <br/>
                <br/>
                Disclosure Of Data
                <br/>
                <br/>
                Legal Requirements
                <br/>
                Who's your Pupper? may disclose your Personal Data in the good faith belief that such
                action is necessary to:<br/>
                ● To comply with a legal obligation<br/>
                ● To protect and defend the rights or property of Who's your Pupper?<br/>
                ● To prevent or investigate possible wrongdoing in connection with the Service<br/>
                ● To protect the personal safety of users of the Service or the public<br/>
                ● To protect against legal liability<br/>
                <br/>
                <br/>
                Security Of Data
                <br/>
                The security of your data is important to us, but remember that no method of
                transmission over the Internet, or method of electronic storage is 100% secure. While
                we strive to use commercially acceptable means to protect your Personal Data, we
                cannot guarantee its absolute security.
                <br/>
                <br/>
                Service Providers
                <br/>
                We may employ third party companies and individuals to facilitate our Service ("Service
                Providers"), to provide the Service on our behalf, to perform Service-related services or
                to assist us in analyzing how our Service is used.
                These third parties have access to your Personal Data only to perform these tasks on
                our behalf and are obligated not to disclose or use it for any other purpose.
                <br/>
                <br/>
                Children's Privacy
                <br/>
                Our Service does not address anyone under the age of 18 ("Children").
                We do not knowingly collect personally identifiable information from anyone under the
                age of 18. If you are a parent or guardian and you are aware that your Children has
                provided us with Personal Data, please contact us. If we become aware that we have
                collected Personal Data from children without verification of parental consent, we take
                steps to remove that information from our servers.
                <br/>
                <br/>
                Changes To This Privacy Policy
                <br/>
                We may update our Privacy Policy from time to time. We will notify you of any changes
                by posting the new Privacy Policy on this page.
                We will let you know via email and/or a prominent notice on our Service, prior to the
                change becoming effective and update the "effective date" at the top of this Privacy
                Policy.
                You are advised to review this Privacy Policy periodically for any changes. Changes to
                this Privacy Policy are effective when they are posted on this page.
                <br/>
                <br/>
                Contact Us
                <br/>
                If you have any questions about this Privacy Policy, please contact us:
                <br/>
                ● By email: mlyangzon@up.edu.ph
            </p>
        </div>
    )
}

export default PrivacyPolicy;